<template>
  <nav class="nav" :class="{ navigation: scrolled }" id="top-nav">
    <logo text="Osama."></logo>
    <button data-toggle="#links" class="toggle btn hide-md">
      <ion-icon name="menu-outline"></ion-icon>
    </button>
    <Links id="links" :links="links" class="links-md links-xs"></Links>
  </nav>
  <router-view />
  <Top></Top>
  <Footer></Footer>
</template>

<script>
import Links from "@/components/global/linkes.vue";
import Logo from "@/components/nav/Logo.vue";
import Footer from "@/components/layout/Footer.vue";
import Top from "@/components/global/toTop.vue";
export default {
  name: "App",
  components: {
    Links,
    Logo,
    Footer,
    Top,
  },
  data: function () {
    return {
      links: [
        { path: "#home", name: "Home", isExact: true },
        { path: "#about", name: "About" },
        { path: "#portfolio", name: "Portfolio" },
        { path: "#contact", name: "Contact Me" },
      ],
      scrolled: false,
    };
  },
  methods: {
    navigator: function () {
      window.addEventListener("scroll", () => {
        if (window.scrollY >= 100) this.scrolled = true;
        else this.scrolled = false;
      });
    },
    toggle: function () {
      let btn = document.querySelectorAll("[data-toggle]");
      for (let i = 0; i < btn.length; i++) {
        let self = btn[0],
          toggle_ele = document.querySelector(self.getAttribute("data-toggle"));
        self.addEventListener("click", () => {
          let display_value = window.getComputedStyle(toggle_ele).display;
          if (display_value != "none") {
            toggle_ele.style.display = "none";
          } else {
            toggle_ele.style.display = "flex";
          }
        });
      }
      if (window.screen.availWidth <= 600) {
        document
          .querySelector("[class*='links-']")
          .addEventListener("click", function () {
            this.style.display = "none";
          });
      }
    },
  },
  mounted: function () {
    this.toggle();
    this.navigator();
  },
};
</script>

<style lang="scss">
nav.navigation {
  box-shadow: 0px -10px 20px 0px #2e2d2d;
  width: 100%;
  position: fixed;
  background-color: $white;

  a {
    color: $six;
  }
  z-index: 99999;
}
.toggle {
  z-index: 99;
}
@media screen and (max-width: 600px) {
  .nav-links {
    margin-right: 0px;
    display: none;
  }
}
</style>
