<template>
  <div class="animate-btn">
    <a :href="to" class="btn-content">
      <ion-icon name="chevron-down-outline"></ion-icon>
    </a>
  </div>
</template>

<script>
export default {
  name: "AnimBtn",
  props: ["to"],
  data: function () {
    return {};
  },
};
</script>

<style type="scss">
.animate-btn {
  margin-bottom: 15px;
  margin-top: 15px;
  font-size: 2em;
  color: #fff;
  cursor: pointer;
  width: 100%;
  height: calc(2em * 0.5);
  position: relative;
}

.btn-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: UpDown 1s ease-in-out infinite;
}

@keyframes UpDown {
  0% {
    top: 50%;
  }

  25% {
    top: 60%;
  }

  50% {
    top: 70%;
  }

  75% {
    top: 80%;
  }

  100% {
    top: 80%;
  }
}
</style>
