<template>
  <div class="container-flow">
    <h2 class="section-title">My Skills</h2>
    <div class="continer">
      <div class="row-column">
        <div class="skills-cards">
          <div class="skill-card" v-for="skill of showdSkill" :key="skill">
            <div class="skill-name">{{ skill.name }}</div>
            <Proggress
              class="skill-proggress"
              :from="0"
              :to="skill.proggress"
              :onScroll="true"
              :time="200"
            ></Proggress>
          </div>
        </div>
        <div class="btn-block center">
          <div class="btn btn-primary btn-shadow btn-no-round" @click="more">
            More
            <span class="icon">
              <ion-icon name="add-circle-outline"></ion-icon>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Proggress from "../global/Proggress.vue";
const axios = require("axios").default;

export default {
  name: "Skills",
  components: { Proggress },
  data: function () {
    return {
      skills: null,
      showdSkill: null,
      showItems: 4,
      to: 4,
      from: 0,
      maxLength: 0,
    };
  },
  methods: {
    getSkils() {
      let skillsApi =
        "https://osama-portfolio.blogspot.com/feeds/posts/default/-/skills?alt=json";
      axios
        .get(skillsApi)
        .then((res) => {
          let skills = res.data.feed.entry[0].content.$t
            .replace(
              new RegExp("([<][\\s]?[\\/]?\\w+[\\s]?[\\/]?[>])", "gm"),
              ""
            )
            .split(",")
            .map((e) => {
              e = e.split(":");
              return { name: e[0], proggress: parseFloat(e[1]) };
            });
          this.skills = skills;
          this.maxLength = skills.length;
          this.updateSkills();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    updateSkills() {
      this.showdSkill = this.skills.slice(this.from, this.to);
    },
    more() {
      if (this.to > this.maxLength) {
        alert("no more");
        return;
      }
      this.to += this.showItems;
      this.updateSkills();
      window.scrollTo(0, scrollY + 100);
    },
  },
  created() {
    this.getSkils();
  },
};
</script>

<style lang="scss">
.container-flow {
  background-color: #eee;
  margin-top: 15px;
  flex-grow: 1;
  padding-bottom: 15px;
}

.skill-card {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  flex-wrap: wrap;
  .skill-proggress {
    width: 100%;
    cursor: pointer;
    flex-basis: 100%;
    margin-top: 5px;
    text-align: center;
    align-items: center;
    box-shadow: inset 3px 2px 4px 0px #8d8a8a;
    border-radius: 10px;
    height: 5px;
    .proggess {
      border-radius: 10px;
      background: $fiveth;
      height: 100%;
    }
  }
}

@media screen and(max-width: 600px) {
  .skill-card {
    margin-bottom: 15px;
    .skill-proggress {
      width: 100%;
      flex-basis: 100%;
      margin: 0px;
      margin-top: 7px;
    }
  }
}
</style>
