/*jshint esversion:6 */
import { createApp } from "vue";
//import "../public/js/app.js";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import "normalize.css";
import "./scss/main.scss";

createApp(App)
  .use(store)
  .use(router)
  .mixin({
    methods: {
      handelScroll: function () {
        //let sections = this.Global;
        document.addEventListener("scroll", () => {
          //console.log(this.$el);
          let temp = this.$el,
            clientHeight = this.$el.clientHeight;
          if (
            temp.offsetTop * 0.75 < scrollY &&
            scrollY < temp.offsetTop + clientHeight - 100
          ) {
            // window.location.hash = this.$el.id;
            window.history.pushState({}, "", "#" + this.$el.id);
            let links = document.querySelector(
              "a[href='#" + this.$el.id + "']"
            );
            [...links.parentElement.children].forEach((e) => {
              e.classList.remove("router-link-active");
            });
            links.classList.add("router-link-active");
          }
        });
      },
    },
  })
  .mount("#app");
