<template>
  <div :title="proggress" class="proggress-container">
    <div :style="{ width: proggress + '%' }" class="progress"></div>
  </div>
</template>

<script>
export default {
  name: "Proggress",
  props: {
    from: Number,
    to: Number,
    time: Number,
    onScroll: Boolean,
  },
  data: function () {
    return {
      proggress: this.from,
      started: false,
    };
  },
  methods: {
    proggressIncress: function () {
      let time = this.time == 0 || this.time == undefined ? 200 : this.time;

      let x = setInterval(() => {
        if (this.proggress >= this.to) {
          this.proggres = this.to;
          clearInterval(x);
          return;
        }
        this.proggress++;
      }, time / this.to);
    },
    ScrollEvent: function () {
      if (this.started == false) {
        document.addEventListener("scroll", () => {
          if (
            Math.abs(Math.floor(this.$el.parentElement.offsetTop * 0.5)) <=
            window.scrollY
          ) {
            this.proggressIncress();
            this.started = true;
          }
        });
      }
    },
  },
  mounted: function () {
    if (this.onScroll) {
      this.ScrollEvent();
    } else {
      this.proggressIncress();
    }
  },
};
</script>

<style lang="scss">
.proggress-container {
  cursor: pointer;
  text-align: center;
  align-items: center;
  box-shadow: inset 3px 2px 4px 0px #8d8a8a;
  border-radius: 10px;
  height: 5px;
  .progress {
    border-radius: 10px;
    background: $fiveth;
    height: 100%;
  }
}
</style>
