<template>
  <div class="float-btn" :class="{ show: show }">
    <a @click="evt" href="#" class="btn circle btn-dark">
      <ion-icon name="chevron-up-circle-outline"></ion-icon>
    </a>
  </div>
</template>

<script>
export default {
  name: "Top",
  data: function () {
    return {
      show: false,
    };
  },
  methods: {
    evt: function () {
      window.scrollTo(0, 0);
    },
  },
  created() {
    document.addEventListener("scroll", () => {
      if (scrollY >= 200) {
        this.show = true;
      } else {
        this.show = false;
      }
    });
  },
};
</script>

<style lang="scss" scoped>
.float-btn {
  position: fixed;
  right: 5%;
  bottom: 5%;
  display: none;
  font-size: 2em;
}
.float-btn a.btn-dark {
  padding: 5px;
}
.show {
  display: inline-block;
}
</style>
