<template>
  <div class="fading-text">
    <div class="fade-text" :class="{ fadein: showed }">
      {{ textVal }}
    </div>
  </div>
</template>

<script>
export default {
  name: "FadeText",
  props: ["listText"],
  data: function () {
    return {
      textVal: "",
      fadinSpeed: 200,
      fadeOutSpeed: 200,
      fadeDelay: 2000,
      showed: false,
      index: 0,
    };
  },
  methods: {
    show() {
      //debugger;
      this.showed = true;
      //this.textVal = this.listText[this.index];
      if (this.index < this.listText.length) {
        if (!this.showed) this.showed = true;
        this.textVal = this.listText[this.index];
        setTimeout(this.hide, this.fadeDelay);
      } else {
        this.index = 0;
      }
    },
    hide() {
      this.showed = false;
      this.index += 1;
      if (this.index >= this.listText.length) {
        this.index = 0;
      }
      setTimeout(this.show, this.fadeOutSpeed);
    },
  },
  created() {
    setTimeout(this.show, this.fadinSpeed);
  },
};
</script>

<style lang="scss">
.fading-text {
  text-transform: capitalize;
  font-weight: bold;
  font-size: 1.2em;
}

.fading-text {
  transition: all 0.2ms ease-in-out;
}

.fadein {
  animation: fadein 1000ms ease-in-out;
}

.fadeout {
  animation: fadeout 200ms ease-in-out;
}

@keyframes fadein {
  0% {
    transform: scale(0);
    opacity: 0;
  }

  50% {
    opacity: 0.5;
  }

  75% {
    // transform: scale(0.75);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fadeout {
  0% {
    transform: scale(1);
  }

  75% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(0);
  }
}
</style>
