<template>
  <span class="counter" v-text="counter"> </span>
</template>

<script>
export default {
  name: "CountAnim",
  props: ["from", "to", "time", "onScroll"],
  data: function () {
    return {
      counter: this.from,
      started: false,
    };
  },
  methods: {
    counterTimer: function () {
      let startTime = new Date().getTime(),
        time = this.time == 0 ? 200 : this.time,
        duration = startTime + time * 1000;
      let x = setInterval(() => {
        if (this.counter >= this.to) return clearInterval(x);
        this.counter++;
      }, Math.abs(Math.floor(duration / this.to)));
    },
    ScrollEvent: function () {
      if (this.started == false) {
        document.addEventListener("scroll", () => {
          if (
            Math.abs(Math.floor(this.$el.parentElement.offsetTop * 0.5)) <=
            window.scrollY
          ) {
            this.counterTimer();
            this.started = true;
          }
        });
      }
    },
  },
  mounted: function () {
    if (this.onScroll) {
      this.ScrollEvent();
    } else {
      this.counterTimer();
    }
  },
};
</script>

