<template>
  <section class="section" id="contact">
    <h2 class="section-title">Contact Me</h2>
    <form class="contact" action="" method="post">
      <div class="row-column center">
        <div class="form-group">
          <input
            type="text"
            class="form-controll"
            name="name"
            placeholder="Your Name"
          />
        </div>
        <div class="form-group">
          <input
            type="email"
            class="form-controll"
            name="email"
            placeholder="Email"
          />
        </div>
        <div class="form-group">
          <textarea
            name="msg"
            class="form-controll"
            placeholder="Message"
            rows="6"
          ></textarea>
        </div>
        <div class="form-group">
          <button class="btn btn-dark submit" type="submit">Send</button>
        </div>
      </div>
    </form>
  </section>
</template>

<script>
export default {
  name: "Contact",
  data: function () {
    return {};
  },
  mounted: function () {
    this.handelScroll.call(this);
  },
};
</script>

<style lang="scss">
.submit {
  width: 10%;
}

@media screen and(max-width: 600px) {
  .form-controll {
    width: 80% !important;
  }
  .submit {
    width: 30%;
  }
}
</style>
