<template>
  <div class="nav-links">
    <a
      v-for="link of links"
      :key="link"
      :href="link.path"
      v-html="link.name"
    ></a>
  </div>
</template>

<script type="text/javascript">
export default {
  name: "Links",
  props: ["links"],
  data: function () {
    return {};
  },
  watch: {
    $route: function (to) {
      let link = document.querySelector(`[href='${to.hash}']`);
      if (link) {
        [...link.parentElement.children].forEach((element) => {
          element.classList.remove("router-link-active");
        });
        link.classList.add("router-link-active");
      }
    },
  },
};
</script>

<style lang="scss">
.nav-links {
  margin-right: 15px;
  a {
    text-decoration: none;
    color: $third;
    text-transform: capitalize;
    &:not(:first-child) {
      margin-left: 15px;
    }
    &:hover,
    &.router-link-active {
      color: $fiveth;
    }
  }
}

@media screen and(max-width: 600px) {
  .nav-links {
    width: 50%;
    height: 100vh;
    background: #fff;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
    a {
      width: 100%;
      text-align: center;
      margin: 0;
      /* margin-bottom: 15px; */
      border-bottom: 1px solid;
      padding: 15px 0px;
    }
  }
}

@media screen and(max-width: 450px) {
  .nav-links {
    width: 100%;
    height: 100vh;
    background: #fff;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
    a {
      width: 100%;
      text-align: center;
      margin: 0;
      /* margin-bottom: 15px; */
      border-bottom: 1px solid;
      padding: 15px 0px;
    }
  }
}
</style>
